<template>
    <div>
        <form id="username_cont" @submit.prevent="addUser">
            <div>
                <input id="input-username" type="text" placeholder="Username" v-model="user">
                <button type="button" @click="$emit('add-user', user)">></button>
            </div>
        </form>
    </div>
</template>

<script>
export default {
    name: 'Username',
    data() {
        return {
            user: ''
        }
    },
    methods: {
        addUser() {
            this.$emit('add-user', this.user)
        }
    }
}
</script>

