<template>
    <div>
        <form @keyup.enter="addText">
            <textarea id="text-area-focus" type="text" v-model="text" placeholder="Some secrets..."></textarea>
            <button type="button" @click="addText">></button>
        </form>
    </div>
</template>

<script>
export default {
    name: 'TextInput',
    data() {
        return {
            text: ''
        }
    },
    methods: {
        addText() {
            this.$emit('add-text', this.text)
            this.text= ''
            document.getElementById('text-area-focus').focus()
        }
    }
}
</script>

