<template>
  <div id="app">
    <Username id="add-user" @add-user="addUser" />
    <header>
      <h1 id="chat-title">s-k-k v-0.0.1</h1>
      <p id="chat-usr">temp user: {{user}}</p>
    </header>
    <Textarea @add-text="addText" :messages="messages" />
    <footer>
      <p>just for a test purpose - do not actually use it</p>
    </footer>
  </div>
</template>

<script>
import Textarea from './components/TextArea'
import randomColor from 'randomcolor'
import Username from './components/Username'
import gsap from 'gsap'
import { db } from './config/db'

export default {
  name: 'App',
  data() {
    return {
      messages: [],
      user: '',
      color: randomColor()
    }
  },
  firebase: {
    messages: db.ref('chat'),
    user: '',
    color: randomColor()
  },
  components: {
    Textarea, Username
  },
  methods: {
    addText(newText) {
      const formattedText = {
          user: this.user,
          text: newText,
          color: this.color
      }
      db.ref('chat').push(formattedText)
      this.checkDBLength()
    },
    checkDBLength() {
      if(this.messages.length > 100) {
        db.ref(`chat/${this.messages[0]['.key']}`).remove()
      }
    },
    adjustChat() {
      var messageBody = document.getElementById('text-area-size');
      messageBody.scrollTop = messageBody.scrollHeight;
    },
    addUser(user) {
      this.user = user
      gsap.to(document.getElementById('add-user'), {autoAlpha:0 , duration: 0.7})
      this.adjustChat()
    }
  }
}
</script>

