<template>
    <div id="text-area">
        <div div id="text-area-size">
            <Textrow v-for="(message, idx) in messages" :key="idx" :message="message" :color="color"/>
        </div>
        <TextInput id="text-input-size" @add-text="addText" />
    </div>
</template>

<script>
import Textrow from './TextRow'
import TextInput from './TextInput'

export default {
    name: 'TextArea',
    props: ['messages', 'color'],
    components: {
        Textrow, TextInput
    },
    methods: {
        addText(newText) {
            this.$emit('add-text', newText)
        }
    }
}
</script>

<style scoped>

#text-area {
    border: 1px solid #eee;
    max-width: 600px;
    margin: 15px auto 0;
    height: 100%;
    height: calc(100vh - 200px);
    padding-top: 10px;
}

#text-area-size {
    height: calc(100% - 52px);
    overflow: scroll;
    padding-bottom: 12px;
}

#text-input-size {
    height: 40px;
}

</style>